.content {
    display: flex;
    justify-content: space-between;
    min-height: 95vh;
    margin: 100px 50px 0 50px;
    align-items: flex-start;
    width: 100%;

}

.blockImg {
    position: relative;
    width: 400px;
    height: 400px;
    background: #3545547d;
    border-radius: 10px;
    transition: 1s ease;
    top: 40px;
}

.blockImg img {
    position: absolute;
    width: 95%;
    height: 95%;
    -o-object-fit: cover;
       object-fit: cover;
    top: 10px;
    left: 10px;
    border-radius: 10px;
    transform: rotate(-13deg);
    transition: .5s ease;
}

.blockImg img:hover {
    transform: rotate(0deg);

}

.blockImg:hover {
    background: #3e5163;
}

.myName {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 180px;
}

.myName h1 {
    font-size: 50px;
    color: #6d8ca9;
    width: 100%;
    text-align: center;
}

.myName h2 {
    font-size: 40px;
    color: #6d8ca9;
    width: 100%;
    text-align: center;
}

.blockMynameP {
    color: #6d8ca9;
    width: 40%;
    text-align: center;
    font-size: 21px;
    letter-spacing: 1px;
    line-height: 35px;
    margin: 0 auto;
}


.myName .btn {
    background-color: #1E2730;
    width: 150px;
    height: 50px;
    color: #6d8ca9;
    font-size: 18px;
    cursor: pointer;
    position: relative;
    transition: .5s ease;
    overflow: hidden;
    border: 1px solid #3e5163;
}

.myName .btn::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 0;
    top: 0;
    border-radius: 0 0 50% 50%;
    background-color: #8dc8ff37;
    transition: .5s ease;
    left: 0;

}

.myName .btn:hover::before {
    height: 180%;

}

.myName .btn:hover {
    color: #ffffff;
    z-index: 2;
}


/* btn active */


.btnActive {
    transform: translateY(30px);
    transition: .7s ease-in-out;
    background-color: #1E2730;
    width: 150px;
    height: 50px;
    color: #6d8ca9;
    font-size: 18px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    border: 1px solid #3e5163;
}

.myName .btnActive::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 0;
    top: 0;
    border-radius: 0 0 50% 50%;
    background-color: #8dc8ff37;
    transition: .5s ease;
    left: 0;

}

.myName .btnActive:hover::before {
    height: 180%;

}

.myName .btnActive:hover {
    color: #ffffff;
    z-index: 2;
}

.navItems {
    display: flex;
    flex-direction: column;
    position: fixed;
    right: 50px;

}

.navItems .item {
    margin-top: 150px;
    border-radius: 50%;
    border: 1px solid #3e5163;
    width: 100px;
    height: 100px;
    background-color: #1e273000;
    color: #6d8ca9;
    transition: .5s ease-in-out;
}

.navItems .item:hover {
    color: #ffffff;
    transition: .5s ease-in-out;
    transform: scale(1.1);
    background-color: #8dc8ff37;
    border-color: #ffffff;
}

.itemLink {
    text-decoration: none;
    color: #6d8ca9;

}












/* section about me */

.aboutMe {
    min-height: 95vh;
}


.aboutMeTitle {
    color: #6d8ca9;
    font-size: 50px;
    text-align: center;
}

.aboutMeContent {
    display: flex;
    justify-content: space-around;
    margin: 0 50px 0 50px;
}

.personalInfo h4 {
    color: #6d8ca9;
    font-size: 40px;
}

.personalInfo p {
    color: #6d8ca9;
    letter-spacing: 1px;
    font-size: 22px;
}

.mySkills {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.mySkills h4 {
    color: #6d8ca9;
    font-size: 40px;
}

.skillsRow1,
.skillsRow2 {
    display: flex;
    margin-top: 10px;

}

.skillsRow1 img,
.skillsRow2 img {
    width: 40px;
    height: 40px;
    z-index: 5;
}

.skillsRow1 span,
.skillsRow2 span {
    font-size: 18px;
    z-index: 5;
    color: #6d8ca9;

}

.skillsItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #3e5163;
    margin: 0 10px;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    position: relative;
    overflow: hidden;
    padding: 5px 5px;

}

.skillsItem::before {
    content: '';
    position: absolute;
    background: linear-gradient(315deg, #00ccff, #d400d4);
    inset: 25px 0;
    transition: .5s;
    -webkit-animation: circle 4s linear infinite;
            animation: circle 4s linear infinite;
}

.skillsItem:hover::before {
    inset: 0px;
}

.skillsItem::after {
    content: '';
    position: absolute;
    inset: 3px;
    background-color: #1E2730;
    border-radius: 50%;
    z-index: 1;
}


@-webkit-keyframes circle {
    0% {
        transform: rotate(0deg);
    }

    0% {
        transform: rotate(-360deg);
    }
}


@keyframes circle {
    0% {
        transform: rotate(0deg);
    }

    0% {
        transform: rotate(-360deg);
    }
}





/* section contact */

.contact {
    min-height: 100vh;
}

.contactTitle {
    color: #6d8ca9;
    font-size: 50px;
    text-align: center;
}

.contactContent {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.contactBlock1,
.contactBlock2,
.contactBlock3 {
    background: #1E2730;
    width: 250px;
    height: 200px;
    border-radius: 10px;
    margin-top: 150px;

}

.contactBlock1 img,
.contactBlock2 img,
.contactBlock3 img {
    width: 50px;
    height: 50px;
}

.telegram,
.viber,
.whatsapp {
    width: 100%;
    background: #3e5163;
    border-radius: 10px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    -webkit-clip-path: circle(15% at 50% 20%);
            clip-path: circle(15% at 50% 20%);
    transition: -webkit-clip-path .5s ease-in-out;
    transition: clip-path .5s ease-in-out;
    transition: clip-path .5s ease-in-out, -webkit-clip-path .5s ease-in-out;
    cursor: pointer;
}

.telegram:hover,
.viber:hover,
.whatsapp:hover {
    -webkit-clip-path: circle(75%);
            clip-path: circle(75%);
    background: linear-gradient(315deg, #00ccff, #d400d4)
}

.telegram p,
.viber p,
.whatsapp p {
    font-size: 21px;
    color: #ffffff;
}



/* social blocks */

.socialBlocks {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.block1,
.block2,
.block3 {
    width: 250px;
    height: 300px;
    position: relative;
    box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
    border-radius: 15px;
    background: rgb(255, 255, 255, 0.1);
    margin-top: 250px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-top: 1px solid rgb(255, 255, 255, 0.5);
    border-left: 1px solid rgb(255, 255, 255, 0.5);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    bottom: 300px;
    right: 50px;
    flex-wrap: wrap;
}

.vanilla {
    position: relative;
    top: 200px;
    width: 150px !important;
    height: 200px !important;
    background: linear-gradient(315deg, #00ccff, #d400d4) !important;
    flex-wrap: wrap;

}

.block1 h3,
.block2 h3,
.block3 h3 {
    font-size: 40px;
    pointer-events: none;
    color: rgb(255, 255, 255, 0.5);
    position: absolute;
    top: 130px;
}

.block1 img,
.block2 img,
.block3 img {
    width: 80px;
    height: 80px;
    margin-top: 80px;
}


/* repository */


.repository {
    min-height: 35vh;
    margin-top: 400px;
}

.repositoryTitle {
    display: flex;
    flex-direction: column;
    align-items: center;

}

.repositoryTitle h2 {
    color: #6d8ca9;
    font-size: 45px;
    width: 100%;
    text-align: center;
}


.repositoryTitle img {
    width: 150px;
    height: 150px;
    -webkit-animation: rotate 6s linear infinite;
            animation: rotate 6s linear infinite;
}




@-webkit-keyframes rotate {
    0% {
        transform: rotateY(0deg);
    }

    100% {
        transform: rotateY(360deg);
    }
}




@keyframes rotate {
    0% {
        transform: rotateY(0deg);
    }

    100% {
        transform: rotateY(360deg);
    }
}










/* media query */



@media screen and (max-width:1700px) {
    .navItems {
        right: 30px;
    }

    .navItems .item {
        width: 80px;
        height: 80px;
    }
}

@media screen and (max-width:1400px) {
    .navItems {
        right: 10px;
    }

    .content {
        flex-direction: column;
        align-items: center;
        margin: 100px auto;
    }

    .myName {
        margin: 100px 0;
    }

    .navItems {
        display: flex;
        position: fixed;
        justify-content: space-around;
        width: 100%;
        right: 0;
        top: -130px;
        flex-direction: row;
        z-index: 7;
    }

    .navItems .item {
        width: 100px;
        height: 100px;
    }
}

@media screen and (max-width:1000px) {
    .navItems .item {
        width: 80px;
        height: 80px;
    }

    .myName h1 {
        font-size: 45px;
    }

    .myName h2 {
        font-size: 35px;
    }

    .blockMynameP {
        font-size: 20px;
        letter-spacing: 0px;
    }

    .aboutMeTitle {
        font-size: 45px;
    }

    .personalInfo h4,
    .mySkills h4 {
        font-size: 35px;
    }

    .personalInfo p {
        font-size: 20px;
    }

    .contactTitle {
        font-size: 45px;
    }

    .repositoryTitle h2 {
        font-size: 40px;
    }
}

@media screen and (max-width:800px) {
    .aboutMeContent {
        flex-direction: column;
        align-items: center;
    }

    .personalInfo h4,
    .mySkills h4 {
        text-align: center;
    }

    .personalInfo p {
        text-align: center;
    }

    .skillsItem {
        margin: 0 85px;
    }

    .contact {
        margin-top: 200px;
    }
}


@media screen and (max-width:700px) {
    .skillsItem {
        margin: 0 65px;
    }

    .socialBlocks {
        flex-direction: column;
    }

    .vanilla {
        margin: 50px 0 !important;
    }

    .repositoryTitle h2 {
        font-size: 35px;
        width: 80%;
    }
}


@media screen and (max-width:600px) {

    .skillsRow1,
    .skillsRow2 {
        flex-direction: column;
    }

    .skillsItem {
        margin-top: 15px;
    }

    .contactContent {
        flex-direction: column;
        align-items: center;
    }

    .myName h1 {
        font-size: 40px;
    }

    .myName h2 {
        font-size: 30px;
    }

    .aboutMeTitle {
        font-size: 40px;
    }

    .personalInfo h4,
    .mySkills h4 {
        font-size: 30px;
    }

    .personalInfo p {
        font-size: 18px;
    }

    .contactTitle {
        font-size: 40px;
    }
}

@media screen and (max-width:500px) {
    .navItems .item {
        width: 60px;
        height: 60px;
        font-size: 10px;
    }

    .blockImg {
        width: 300px;
        height: 300px;
    }

    .myName h1 {
        font-size: 30px;
    }

    .myName h2 {
        font-size: 25px;
    }

    .aboutMeTitle {
        font-size: 30px;
    }

    .personalInfo h4,
    .mySkills h4 {
        font-size: 25px;
    }

    .personalInfo p {
        font-size: 16px;
    }

    .contactTitle {
        font-size: 30px;
    }

    .repositoryTitle h2 {
        font-size: 30px;
        width: 90%;
    }

    .repositoryTitle img {
        width: 100px;
        height: 100px;
    }
}

@media screen and (max-width:415px) {
    .blockImg {
        width: 250px;
        height: 250px;
    }

    .blockImg img {
        top: 6.5px;
        left: 6.5px;
    }

    .blockMynameP {
        font-size: 16px;
        width: 80%;
    }
}